html {
  font-size: 1.05vw;
}

body {
  color: #4a4848;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

.search-form {
  background-color: #7fccff;
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
  position: sticky;
  top: 0;
}

.search-input {
  width: 250px;
  height: 30px;
  color: #4a4848;
  border: 1px solid gray;
  border-radius: 10px;
  outline: none;
  padding-left: 10px;
}

.search-input:focus, .search-input:hover {
  border: 2px solid gray;
}

.search-btn:focus, .search-btn:hover {
  background-color: #4a4848;
}

.search-btn {
  width: 30px;
  height: 30px;
  background-color: gray;
  border: none;
  border-radius: 10px;
  outline: none;
  justify-content: center;
  align-items: center;
  margin-left: 220px;
  display: flex;
  position: absolute;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
}

.photo-card {
  min-width: 150px;
  width: calc(25% - 15px);
  border: none;
  box-shadow: 0 1px 1px #0000001f, 0 4px 4px #0000000f, 1px 4px 6px #00000029;
}

.photo-card:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 70%;
  width: 100%;
  object-fit: cover;
  display: block;
}

.info {
  height: 30%;
  border-bottom: 1px solid #eee;
  border-left: 1px solid #eee;
  border-right: 1px solid #eee;
  flex-wrap: wrap;
  justify-content: center;
  justify-items: center;
  padding: 20px 0;
  display: flex;
}

.info-item {
  text-align: center;
}

.info-item:not(:last-child) {
  margin-right: 10px;
}

.load-more {
  text-transform: uppercase;
  color: #4a4848;
  background-color: #7fccff;
  border: none;
  border-radius: 10px;
  margin-left: auto;
  margin-right: auto;
  padding: 5px 10px;
}

.load-more:focus, .load-more:hover {
  background-color: #1991e1;
}

